<template>
  <div
    :class="inputIsFocused ? 'input-box c-edition focused' : 'input-box c-edition'"
  >
    <input
      class="input"
      type="text"
      autocomplete="off"
      @focus="foucusInput()"
      @blur="unFocusInput()"
      @keyup.enter="
        addTodo({
          text: todoData.input,
          id: getId(),
          buildTime: timeStemp(),
          lastUpdate: timeStemp(),
          colorLabel: todoData.currentColor
        })
      "
      v-model="todoData.input"
      :placeholder="todoData.inputPlaceHolder"
    />
    <div class="ctrl-box">
      <div
        class="small-btn color-label"
        @click="addColorLabel()"
        :style="{ background: todoData.currentColor }"
      >
        <div class="color-box" v-show="isShowColorLabel">
          <i
            v-for="(col, index) in colors"
            class="color"
            @click="selectColor(col)"
            :style="{ background: col }"
            :key="index"
          >
          </i>
        </div>
      </div>
      <div
        class="small-btn icon fa fa-plus-circle"
        @click="
          addTodo({
            text: todoData.input,
            id: getId(),
            buildTime: timeStemp(),
            lastUpdate: timeStemp(),
            colorLabel: todoData.currentColor
          })
        "
      ></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getTimeStemp, createId } from '@/assets/lib/myLib';

export default {
  name: 'AddTodoList',

  data() {
    return {
      todoData: {
        inputPlaceHolder: '在这里添加TODO',
        input: '',
        currentColor: '#ff6700',
      },
      isShowColorLabel: false,
      inputIsFocused: false,
    };
  },

  computed: {
    ...mapState(['colors']),
  },

  methods: {
    ...mapMutations(['addTodo']),

    addColorLabel() {
      this.isShowColorLabel = !this.isShowColorLabel;
    },

    selectColor(color) {
      this.todoData.currentColor = color;
    },

    foucusInput() {
      this.inputIsFocused = true;
    },

    unFocusInput() {
      this.inputIsFocused = false;
    },

    // 生产时间戳
    timeStemp() {
      const time = getTimeStemp();
      return time;
    },
    // 生成一个不会重复的id值
    getId() {
      const id = createId();
      return id;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/lib/scss/mixins.scss";
@import "@/assets/lib/scss/config.scss";

.input-box {
  display: flex;
  flex-wrap: nowrap;
  margin: 36px auto;
  width: 100%;
  height: 64px;
  padding: 0 24px;
  background-color: $cl-aux1;
  border-radius: 8px;
  border: 1px solid $cl-aux2;
  box-shadow: 0 4px 12px $cl-aux3;
  transition: all 0.3s ease-out;

  &.focused {
    box-shadow: 0 16px 24px -12px $cl-aux4;
  }

  @include respond-to(lg) {
    width: 1026px;
  }

  .input {
    flex: 1;
    height: 100%;
    border: none;
    font-size: 18px;
  }

  .color-label {
    position: relative;
    margin-right: 16px;

    .color-box {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: space-around;
      top: 26px;
      right: -26px;
      width: 220px;
      height: 50px;
      border-radius: 12px;
      background-color: $cl-aux1;
      box-shadow: 0 8px 16px -2px $cl-aux4;
      cursor: default;

      .color {
        display: block;
        width: 24px;
        height: 24px;
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.2s linear;

        &:hover {
          width: 30px;
          height: 30px;
          box-shadow: 0 2px 8px -1px $cl-font3;
          transition: all 0.2s linear;
        }
      }
    }
  }
}

.icon {
  color: $cl-main1;
  font-size: 28px;
}
</style>



